<template>
  <div class="course">
    <!-- header -->
    <header>
      <h3>特色课</h3>
      <div class="right">
        <img
          src="data:image/png;base64,"
          alt
          class="search"
          @click="back"
        />
      </div>
    </header>
    <!-- nav -->
    <van-dropdown-menu class="nav" active-color="#EE8032">
      <van-dropdown-item title="分类">
        <!--option one -->
        <div class="nkl_option1">
          <ul>
            <li v-for="(item,index) of attrclassify" :key="index">
              <p>{{ item.name }}</p>
              <ul>
                <li v-for="(v,i) of item.child" :key="i">{{ v.name }}</li>
              </ul>
            </li>
          </ul>
          <div>
            <button>重置</button>
            <button>确定</button>
          </div>
        </div>
      </van-dropdown-item>
      <!-- option two -->
      <van-dropdown-item title="排序">
        <div class="nkl_option2">
          <p v-for="(item,index) in option2" :key="index">{{item}}</p>
        </div>
      </van-dropdown-item>
      <!-- option three -->
      <van-dropdown-item title="筛选">
        <div class="nkl_option3">
          <span v-for="(item,index) in option3" :key="index">{{item}}</span>
        </div>
      </van-dropdown-item>
    </van-dropdown-menu>

    <!-- mine -->
    <ul class="index-item-page">
      <li class="ii-item" v-for="(v,i) of CourseList" :key="i" @click="toCourseD(v.id)">
        <p class="ii-title">{{ v.title }}</p>
        <p class="ii-time">{{ v.start_play_date | ttime(v.end_play_date) }} | 共{{ v.total_periods }}课时</p>
        <div class="ii-teacher">
          <img :src="v.teachers_list[0].teacher_avatar" alt />
          <font>{{ v.teachers_list[0].teacher_name }}</font>
          <i class="has-buy" v-show="v.has_buy==1"></i>
        </div>
        <p class="ii-info">
          <span class="person">{{ v.sales_num }}人已报名</span>
          <span class="free" v-if="v.price ==0">免费</span>
          <span class="price" v-else>
            <img src="../assets/pic/gold.png" alt />
            {{ v.price | much }}
          </span>
        </p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      option1: {
        nianji: ["初一", "初二", "初三", "高一", "高二"],
        xueke: ["语文", "数学", "英语", "物理", "化学"]
      },
      option2: ["综合排序", "最新", "最热", "价格从低到高", "价格从高到低"],
      option3: [
        "全部",
        "大班课",
        "小班课",
        "公开课",
        "点播课",
        "面授课",
        "音频课",
        "系统课",
        "图文课",
        "会员课"
      ],
      page: 1,
      list: [],
      loading: false,
      CourseList: [],
      attrclassify: []
    };
  },
  created() {
    this.ajaxCourseClassify();
    this.ajaxCourseBasis();
  },
  filters: {
    much(val) {
      return (val / 100).toFixed(2);
    },
    ttime(t1,t2){
        function getime(numstr) {
        let t = new Date(numstr - 0),
            tarr = [
            t.getFullYear(),
            t.getMonth() + 1,
            t.getDate(),
            t.getHours(),
            t.getMinutes(),
            t.getSeconds()
            ].map(function(m) {
            return ("" + m).padStart(2, "0");
            }),
            str = tarr[1] + "月" + tarr[2] + "日 " + tarr[3] + ":" + tarr[4];
            return str;
        }
      return getime(t1) + " ~ " + getime(t2);
    }
    
  },
  props: {},
  components: {},
  mounted() {},
  methods: {
    back() {
      this.$router.push("/zjySearch");
    },
    async ajaxCourseClassify() {
      let { data: res } = await this.$http.get(
        // "https://www.365msmk.com/api/app/courseClassify?"
        "/api/app/courseClassify?"
      );
      this.attrclassify = res.data.attrclassify;
    },
    async ajaxCourseBasis() {
      let { data: res } = await this.$http.get(
        // "https://www.365msmk.com/api/app/courseBasis?page=1&limit=10&"
        "/api/app/courseBasis?page=1&limit=10&"
      );
      console.log(res);
      this.CourseList = res.data.list;
    },
    toCourseD(id) {
      console.log(id);
      this.$router.push("/course-detail?id=" + id);
    }
  }
};
</script>

<style scoped lang='scss'>
.course {
  background: #f0f2f5;
  min-height: 100%;
  font-size: 0.315rem;
  width: 100%;
}
header {
  position: fixed;
  top: 0;
  z-index: 4;
  width: 100%;
  height: 0.9rem;
  background: #fff;
  h3 {
    margin: 0;
    font-size: 0.36rem;
    color: #595959;
    position: absolute;
    left: 50%;
    top: 25%;
    transform: translateX(-50%);
    font-weight: normal;
  }
  .right {
    position: absolute;
    right: 5%;
    top: 25%;
    height: 100%;
    float: right;
    font-size: 0.4rem;
    color: #595959;

    .search {
      width: 0.45rem;
      height: 0.45rem;
    }
  }
}
.nav {
  width: 100%;
  position: fixed;
  top: 0.89rem;
  background: #fff;
  line-height: 0.9rem;
  left: 0%;
  z-index: 6;
  overflow: hidden;
  .nkl_option1 {
    > ul {
      padding-left: 0.4rem;
      > li {
        > p {
          height: 0.5rem;
          line-height: 0.5rem;
          font-size: 0.24rem;
          color: #595959;
          text-align: left;
        }
        > ul {
          display: flex;
          flex-wrap: wrap;
          overflow: hidden;
          > li {
            width: 1.55rem;
            height: 0.6rem;
            line-height: 0.6rem;
            float: left;
            background: #f5f5f5;
            color: #646464;
            margin-right: 0.2rem;
            margin-bottom: 0.15rem;
            font-size: 0.24rem;
          }
        }
      }
    }
    button:nth-of-type(1) {
      width: 3.34rem;
      height: 0.66rem;
      line-height: 0.66rem;
      border: 0.02rem solid #d8d8d8;
      font-size: 0.3rem;
      background: #fff;
      border-radius: 0.08rem;
      margin-right: 0.19rem;
      color: #6f6f6f;
    }
    button:nth-of-type(2) {
      width: 3.34rem;
      height: 0.66rem;
      line-height: 0.66rem;
      background: #eb6100;
      border-radius: 0.08rem;
      border: 0.02rem solid #eb6100;
      color: #fff;
      font-size: 0.3rem;
    }
  }
}

.index-item-page {
  margin-top: 1rem;
  padding: 0.4rem;
  .ii-item {
    background: #fff;
    border-radius: 0.15rem;
    padding: 0 0.35rem;
    margin-bottom: 0.4rem;
    // position: relative;
    .ii-title {
      padding-top: 0.4rem;
      text-align: left;
      font-size: 0.3rem;
      color: #333;
      font-weight: 400;
    }
    .ii-time {
      margin: 0;
      height: 0.8rem;
      line-height: 0.8rem;
      font-size: 0.28rem;
      color: #666;
      font-weight: 400;
      text-align: left;
    }
    .ii-teacher {
      height: 1.2rem;
      display: flex;
      align-items: center;
      position: relative;
      > img {
        width: 0.72rem;
        height: 0.72rem;
        border-radius: 50%;
      }
      > font {
        font-size: 0.28rem;
        color: rgba(0, 0, 0, 0.45);
        margin-left: 0.2rem;
        font-weight: 400;
      }
      .has-buy {
        position: absolute;
        right: 0rem;
        top: 0rem;
        background: url(../assets/pic/yes.png) no-repeat;
        background-size: 100% 100%;
        width: 1rem;
        height: 1rem;
      }
    }
    .ii-info {
      margin: 0;
      border-top: 0.01rem solid #f5f5f5;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 1rem;
      .person {
        font-size: 0.28rem;
        color: rgba(0, 0, 0, 0.45);
      }
      .free {
        color: #44a426;
        font-size: 0.3rem;
      }
      .price {
        color: #ee1f1f;
        font-size: 0.3rem;
        > img {
          width: 0.32rem;
        }
      }
    }
  }
}
</style>