<template>
  <div>
    <van-nav-bar left-text left-arrow @click-left="onClickLeft" />
    <div class="header">
      <img src="../assets/login-log.png" alt />
    </div>
    <div class="login">
      <p>
        <input type="text" v-model="tel" placeholder="请输入手机号" />
        <span :class="isCode?'getOrange':'getgray'" @click="hy">{{ text }}</span>
      </p>
      <p>
        <input type="text" v-model="psd" placeholder="请输入验证码" />
      </p>
      <div class="box">
        <div class="login-txt" @click="loginO">登录</div>
      </div>
    </div>
    <p>
      <span>*未注册的手机号将自动注册</span>
      <span @click="toLogin">密码登录</span>
    </p>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  name: "",
  data() {
    return {
      tel: "",
      psd: "",
      isCode: true,
      time: 60,
      text: "获取验证码"
    };
  },
  props: {},
  created() {},
  mounted() {},
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    hy() {
      if (this.isCode) {
        if (!/^1[345789]\d{9}$/.test(this.tel) || this.tel == "") {
          Toast("手机号格式错误");
          return false;
        } else {
          this.$http
            .post("/api/app/smsCode", { mobile: this.tel, sms_type: "login" })
            .then(res => {
              Toast("验证码发送成功");
              let time2 = setInterval(() => {
                this.time--;
                this.text = `获取验证码(${this.time})`;
                this.isCode = false;
                if (this.time == 0) {
                  this.text = "获取验证码";
                  this.isCode = true;
                  this.time = 60;
                  clearInterval(time2);
                }
              }, 500);
            });
        }
      }
    },
    loginO() {
      this.$http
        .post("/api/app/login", {
          mobile: this.tel,
          sms_code: this.psd,
          type: 2,
          client: 1
        })
        .then(res => {
          if (res.data.code == 200) {
            window.localStorage.setItem("userID", res.data.data.id);
            window.localStorage.setItem(
              "userToken",
              res.data.data.remember_token
            );
            window.localStorage.setItem("tel", this.tel);
            window.localStorage.setItem("sms_code", this.psd);
            Toast("登录成功");
            if (res.data.data.is_new == 2) {
              this.$router.push("/set-pass");
            } else {
              this.$router.push("/index");
            }
          }
        });
    },
    toLogin() {
      this.$router.push('/login')
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>

<style scoped lang="scss">
.header {
  width: 90%;
  margin: 30px auto;
  img {
    width: 100%;
  }
}
.login {
  width: 100%;
  p {
    height: 50px;
    text-align: left;
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
  }
  input {
    width: 60%;
    height: 90%;
    font-size: .3rem;
    border: none;
    outline: none;
  }
  span {
    color: #fff;
    font-size: 14px;
    padding: 5px 10px;
    height: 20px;
  }
  .getOrange {
    color: #eb6100;
  }
  .getgray {
    color: rgb(204, 204, 204);
  }
}
.box {
  width: 100%;
  .login-txt {
    margin: 20px auto;
    background: rgb(245, 76, 33);
    color: #fff;
    border-radius: 15px;
    text-align: center;
    width: 80%;
    height: 50px;
    line-height: 50px;
    font-size: 0.3rem;
  }
}
p {
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #a5a3a3;
  font-size: 14px;
}
</style>