import axios from "axios"

import {
  Guid
} from './guid'
// 获取设备id
let DeviceID = localStorage.DeviceID;
if (!DeviceID) {
  DeviceID = Guid.NewGuid().ToString('D');
  localStorage.DeviceID = DeviceID;
}
let ID = DeviceID

// axios默认的公共地址
axios.defaults.baseURL = 'http://120.53.31.103:84';
// axios.defaults.baseURL = 'https://wap.365msmk.com';

console.log(ID)
// 添加请求拦截器
axios.interceptors.request.use(function (config) {
  let appToken = window.localStorage.getItem('userToken')
  console.log(config)
  config.headers = {
    DeviceId: ID,
    DeviceType: "H5",
    Authorization: `Bearer ${ appToken?appToken:'' }`
  }
  if(appToken) {
    config.headers.Authorization = `Bearer ${ appToken?appToken:'' }`
  }
  // 在发送请求之前做些什么
  return config;
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  return response;
}, function (error) {
  // 对响应错误做点什么
  return Promise.reject(error);
});


export default axios