<template>
  <div class="course-detail">
    <div class="header">
      <van-icon name="arrow-left" @click="back" />
      <div>课程详情</div>
      <img src="../assets/pic/share.png" alt />
    </div>
    <div class="cd-body">
      <div class="cd-info">
        <p class="info-title">{{ info.title }}</p>
        <i v-show="info.is_collect == 1" class="ic-active" @click="collect(info.is_collect)"></i>
        <i v-show="info.is_collect == 0" class="info-collect" @click="collect(info.is_collect)"></i>
        <p class="info-price cd-free">
          <span v-if="info.price == 0">免费</span>
          <span class="price" v-else>
            <img src="../assets/pic/gold.png" alt />
            {{ info.price | much }}
          </span>
        </p>
        <div>
          <p class="info-com">共{{ info.total_periods }}课时 | {{ info.sales_num }}已报名</p>
          <p class="info-com">
            开课时间:{{ info.start_play_date | nowtime(info.start_play_year) }}
            -
            {{ info.end_play_date | nowtime(info.start_play_year) }}
          </p>
        </div>
      </div>
      <div class="cd-teachers com-style">
        <p class="cd-title">教师团队</p>
        <ul>
          <li v-for="(item,index) of teacher" :key="index" @click="toTeacher(item)">
            <img :src="item.teacher_avatar" alt />
            <span>{{ item.teacher_name }}</span>
          </li>
        </ul>
      </div>
      <div class="cd-tro com-style">
        <p class="cd-title">课程介绍</p>
        <div class="cd-details" v-html="info.course_details"></div>
      </div>
      <div id="list" class="cd-list com-style">
        <p class="cd-title">课程大纲</p>
        <div class="charpter">
          <div>
            <section class="period" v-for="(item,index) in Syllabus" :key="index">
              <div class="charp-title">
                <span class="huifang">回放</span>
                {{item.periods_title}}
              </div>
              <p class="charp-time">
                <span v-for="(m,i) in item.teachers" :key="i">{{m.teacher_name}}</span>
                <span>{{item.start_play}} - {{item.end_play}}</span>
              </p>
            </section>
          </div>
        </div>
      </div>
      <div id="com" class="cd-comment com-style">
        <p class="cd-title">课程评论</p>
        <div class="com-item-page">
          <div role="feed" class="van-list">
            <ul>
              <li class="feed-li" v-for="(item,index) of courseComment" :key="index">
                <div class="evaluate-top">
                  <img :src="item.avatar" />
                  <div class="evaluate-t-left">
                    <div class="info">
                      <span>{{ item.nickname }}</span>
                      <div tabindex="0" role="radiogroup" class="van-rate">
                        <van-rate v-model="item.grade" color="rgb(234, 122, 47)" />
                      </div>
                      <span class="times">{{ item.created_at | tradetime }}</span>
                    </div>
                    <div class="evaluate-btom">{{ item.content }}</div>
                  </div>
                </div>
              </li>
            </ul>
            <div class="van-list__placeholder"></div>
          </div>
          <!---->
        </div>
      </div>
    </div>
    <div class="btn">立即学习</div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      info: [],
      teacher: [],
      Syllabus: [],
      value: 1,
      courseComment: []
    };
  },
  props: {},
  created() {
    this.ajaxCourseInfo();
    this.ajaxSyllabus();
    this.ajaxcourseComment();
  },
  mounted() {},
  methods: {
    async ajaxCourseInfo() {
      let { data: res } = await this.$http.get(
        // `https://www.365msmk.com/api/app/courseInfo/basis_id=${this.$route.query.id}?`
        `/api/app/courseInfo/basis_id=${this.$route.query.id}?`
      );
      this.info = res.data.info;
      this.teacher = res.data.teachers;
    },
    async ajaxSyllabus() {
      let { data: res } = await this.$http.post(
        // `https://www.365msmk.com/api/app/courseChapter`,
        `/api/app/courseChapter`,
        {
          id: this.$route.query.id
        }
      );
      this.Syllabus = res.data;
    },
    async ajaxcourseComment() {
      let { data: res } = await this.$http.post(
        // `https://www.365msmk.com/api/app/courseComment`,
        `/api/app/courseComment`,
        {
          id: 287,
          limit: 10,
          page: 1
        }
      );
      this.courseComment = res.data.list;
    },
    toTeacher(i) {
      this.$router.push("/teacher?id=" + i.teacher_id);
    },
    back() {
      this.$router.go(-1);
    },
    collect(num) {
    console.log(this.info);
      if (num == 0) {
        this.$http
          .post(`/api/app/collect`, {
            course_basis_id: this.$route.query.id,
            type: 1
          })
          .then(res => {
            this.ajaxCourseInfo();
          });
      } else if (num == 1) {
        this.$http
          .post(`/api/app/collect/cancel/${this.info.collect_id}/1`)
          .then(res => {
            this.ajaxCourseInfo();
          });
      }
    }
  },
  computed: {},
  watch: {},
  filters: {
    much(val) {
      return (val / 100).toFixed(2);
    },
    nowtime(t, y) {
      let time = new Date(t),
        tarr = [
          time.getFullYear(),
          time.getMonth() + 1,
          time.getDate(),
          time.getHours(),
          time.getMinutes(),
          time.getSeconds()
        ].map(function(m) {
          return ("" + m).padStart(2, "0");
        }),
        str = y + "." + tarr[1] + "." + tarr[2] + " " + tarr[3] + ":" + tarr[4];
      return str;
    },

    tradetime(t1) {
      let t = new Date(t1 + "000" - 0),
        tarr = [
          t.getFullYear(),
          t.getMonth() + 1,
          t.getDate(),
          t.getHours(),
          t.getMinutes()
        ].map(function(m) {
          return ("" + m).padStart(2, "0");
        }),
        str =
          tarr[0] +
          "-" +
          tarr[1] +
          "-" +
          tarr[2] +
          " " +
          tarr[3] +
          ":" +
          tarr[4];
      return str;
    }
  },
  directives: {},
  components: {}
};
</script>

<style scoped lang="scss">
.course-detail {
  min-height: 100%;
}
.van-icon {
  font-size: 0.3rem;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.15rem;
  box-sizing: border-box;
  height: 0.8rem;
  border-bottom: 1px solid #f0f2f5;
  > div {
    font-size: 0.3rem;
  }
  > img {
    width: 0.4rem;
    height: 0.4rem;
  }
}

.cd-body {
  margin-top: 0.8rem;
  background: #f0f2f5;
  padding-bottom: 1.5rem;
  box-sizing: border-box;
  min-height: 90%;
  p {
    text-align: left;
    margin: 0;
  }
  .cd-info {
    padding: 0.3rem;
    background: #fff;
    position: relative;
    .info-title {
      font-size: 0.24rem;
      font-weight: 400;
      color: #333;
      padding-right: 1rem;
      line-height: 0.5rem;
    }
    .info-collect {
      position: absolute;
      right: 0.26rem;
      top: 0.1rem;
      width: 0.6rem;
      height: 0.6rem;
      background: url(../assets/pic/start-a.png) no-repeat 50%;
      background-size: 0.4rem 0.4rem;
    }
    .ic-active {
      position: absolute;
      right: 0.26rem;
      top: 0.1rem;
      width: 0.6rem;
      height: 0.6rem;
      background: url(../assets/pic/starto.png) no-repeat 50%;
      background-size: 0.4rem 0.4rem;
    }
    .info-price {
      font-size: 0.3rem;
      font-weight: 500;
      color: #ee1f1f;
      line-height: 0.8rem;
    }
    .cd-free {
      color: #eb6100;
    }
    > div {
      .info-com {
        font-size: 0.28rem;
        font-weight: 400;
        color: rgba(0, 0, 0, 0.45);
        line-height: 0.8rem;
      }
    }
  }
  .com-style {
    margin-top: 0.4rem;
    background: #fff;
    padding: 0.13rem 0.25rem;
    .cd-title {
      font-size: 0.26rem;
      font-weight: 500;
      color: #262626;
      padding-top: 0.25rem;
    }
    > ul {
      display: flex;
      flex-wrap: wrap;
      padding: 0.15rem 0;
      > li {
        padding: 0.2rem 0;
        width: 20%;
        height: 1.5rem;
        display: flex;
        align-items: center;
        flex-direction: column;
        box-sizing: border-box;

        img {
          width: 0.8rem;
          height: 0.8rem;
          border-radius: 50%;
        }
        span {
          padding: 0.2rem 0.08rem 0;
          padding-top: 0.1rem;
          font-size: 0.22rem;
          font-weight: 400;
          color: #595959;
        }
      }
    }
  }
  .cd-tro .cd-details {
    font-size: 0.22rem;
    padding: 0.4rem 0.1rem;
  }
  .cd-list {
    .charpter {
      padding: 0.2rem 0.26rem;
      section.period {
        padding: 0.1rem 0 0 0;
        .charp-title {
          text-align: left;
          position: relative;
          padding-left: 0.4rem;
          line-height: 0.8rem;
          font-size: 0.26rem;
          font-weight: 400;
          color: #595959;
          span.huifang {
            background: #ea7a2f;
            display: inline-block;
            width: 0.8rem;
            margin-right: 0.1rem;
            padding: 0 0.13333rem;
            height: 0.38rem;
            border-radius: 0.05333rem;
            font-size: 0.26rem;
            font-weight: 400;
            color: hsla(0, 0%, 100%, 0.85);
            line-height: 0.38rem;
            text-align: center;
          }
        }

        .charp-title:after {
          content: "";
          position: absolute;
          left: 0.05333rem;
          left: 0.53333vw;
          top: 50%;
          transform: translateY(-50%);
          width: 0.10667rem;
          width: 1.06667vw;
          height: 0.10667rem;
          height: 1.06667vw;
          border-radius: 50%;
          background: #eb6100;
        }

        .charp-time {
          font-size: 0.26rem;
          color: #8c8c8c;
          padding-left: 0.13rem;
          padding-bottom: 0.2rem;
          > span {
            padding-left: 0.2rem;
          }
        }
      }
    }
  }
  .feed-li {
    padding: 0.26rem 0.26rem;
    .evaluate-top {
      display: flex;
      border-bottom: 1px solid #f1f1f1;
      img {
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 50%;
      }
    }
    .evaluate-top:last-child {
      border: 0;
    }
    .evaluate-top .evaluate-t-left {
      flex: 1;
    }
    .evaluate-t-left {
      .info {
        display: flex;
        padding-left: 0.2rem;
        align-items: center;
        span:first-child {
          font-size: 0.22rem;
          font-weight: 400;
          color: #333;
        }
        .van-rate {
          margin-left: 0.2rem;
          margin-top: 0;
          height: 0.4rem;
          box-sizing: border-box;
        }
        .times {
          flex: 1;
          font-size: 0.2rem;
          font-weight: 400;
          color: #999;
          text-align: right;
        }
      }
      .evaluate-btom {
        padding-left: 0.2rem;
        padding-top: 0.1rem;
        line-height: 0.3rem;
        color: #999;
        font-size: 0.22rem;
        text-align: left;
      }
    }
  }
}
.btn {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0.8rem;
  background: #eb6100;
  color: #fff;
  font-size: 0.3rem;
  text-align: center;
  line-height: 0.8rem;
}
</style>