<template>
  <div>
    <div class="header">
      <img src="../assets/login-log.png" alt />
    </div>
    <div class="login">
      <p>
        <input type="text" v-model="tel" placeholder="请输入手机号" />
      </p>
      <p>
        <input type="password" v-model="psw" placeholder="请输入密码" />
      </p>
      <div class="box">
        <div class="login-txt" @click="loginO">登录</div>
      </div>
    </div>
    <p>
      <span>找回密码</span>
      <span @click="toSms_login">注册/验证码登录</span>
    </p>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  name: "",
  data() {
    return {
      tel: "",
      psw: ""
    };
  },
  props: {},
  created() {},
  mounted() {},
  methods: {
    toSms_login() {
      this.$router.push("/sms-login");
    },
    loginO() {
      console.log(123);
      this.$http
        .post("/api/app/login", {
          mobile: this.tel,
          password: this.psw,
          type: 1,
          client: 1
        })
        .then(res => {
          console.log(res);

          if (res.data.code == 200) {
            window.localStorage.setItem(
              "userToken",
              res.data.data.remember_token
            );
            this.$router.push("/index");
          } else {
            Toast(res.data.msg);
          }
        });
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>

<style scoped lang="scss">
.header {
  width: 90%;
  margin: 30px auto;
  img {
    width: 100%;
  }
}
.login {
  width: 100%;
  p {
    height: 50px;
    text-align: left;
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
  }
  input {
    width: 60%;
    height: 90%;
    font-size: 0.3rem;
    border: none;
    outline: none;
  }
}
.box {
  width: 100%;
  .login-txt {
    margin: 20px auto;
    background: rgb(245, 76, 33);
    color: #fff;
    border-radius: 15px;
    text-align: center;
    width: 80%;
    height: 50px;
    line-height: 50px;
    font-size: 0.3rem;
  }
}
p {
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #a5a3a3;
  font-size: 14px;
}
</style>