<template>
  <div id="teacher-page">
    <div class="box">
      <div class="header">
        <header>
          <van-icon name="arrow-left" @click="back" />
          <span>讲师详情</span>
          <span></span>
        </header>
      </div>
      <div class="teacher-info">
        <div class="ti-base">
          <img alt :src="teacher.avatar" />
          <div>
            <p>
              <!-- <span>{{ teacher.teacher_name }}</span> -->
              <span>{{ teacher.real_name }}</span>
              <font>{{ teacher.level_name }}</font>
            </p>
            <p>
              {{ teacher.sex==0?'男':'女' }}
              <font>{{ teacher.teach_age }}年教龄</font>
            </p>
          </div>
          <button v-show="flag == 2" @click="heart">关注</button>
          <span class="flagt" v-show="flag==1" @click="heart">已关注</span>
        </div>
        <ul class="ti-label" v-if="teacher.tag_content!=null">
          <li v-for="(item,index) in teacher.tag_content" :key="index">{{ item }}</li>
        </ul>
      </div>
      <van-tabs v-model="active">
        <van-tab title="讲师介绍">
          <ul class="tj">
            <li v-for="(item,index) of jsList.attr" :key="index">
              <span>{{ item.attr_name }}</span>
              <font>{{ item.attr_value[0].attr_value_name }}</font>
            </li>
            <li>
              <span>老师介绍</span>
              <font>{{ jsList.intro }}</font>
            </li>
          </ul>
        </van-tab>
        <van-tab title="主讲课程">
          <ul class="index-item-page">
            <li class="ii-item" v-for="(item,index) of zhj.list" :key="index">
              <p class="ii-title">{{ item.title }}</p>
              <p class="ii-time">共{{ item.total_periods }}课时</p>
              <div class="ii-teacher">
                <img :src="item.teachers_list[0].teacher_avatar" alt />
                <font>{{ item.teachers_list[0].teacher_name }}</font>
              </div>
              <p class="ii-info">
                <span class="person">{{ item.sales_num }}人已报名</span>
                <span class="free" v-if="item.price ==0">免费</span>
                <span class="price" v-else>
                  <img src="../assets/pic/gold.png" alt />
                  {{ item.price | much }}
                </span>
              </p>
            </li>
          </ul>
        </van-tab>
        <van-tab title="学员评价">
          <img src="../assets/pic/nothing-heart.png" alt />
          <p class="no">暂无评价</p>
        </van-tab>
      </van-tabs>
    </div>

    <button class="yue">立即预约</button>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      flag: "",
      teacher: [],
      active: 0,
      jsList: [],
      zhj: [],
      pj: []
    };
  },
  created() {
    console.log(this.$route.query.id);
    this.ajaxTeacherD();
    this.ajaxTeacherC();
    this.ajaxTeacherInfo();
    this.ajaxTeacherMainCourse();
    this.ajaxTeacherComment();
  },
  mounted() {},
  filters: {
    much(val) {
      return (val / 100).toFixed(2);
    }
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    async ajaxTeacherD() {
      let { data: res } = await this.$http.get(
        // `https://www.365msmk.com/api/app/teacher/${this.$route.query.id}?page=1&&limit=2`
        `/api/app/teacher/${this.$route.query.id}?page=1&&limit=2`
      );
      this.teacher = res.data.teacher;
      console.log(this.teacher)
    //   this.flag = res.data.flag;
    },


    async ajaxTeacherC() {
      let { data: res } = await this.$http.get(
        `/api/app/teacher/${this.$route.query.id}?page=1&&limit=2`
      );
      this.flag = res.data.flag;
    },

    async ajaxTeacherInfo() {
      let { data: res } = await this.$http.get(
        // `https://www.365msmk.com/api/app/teacher/info/${this.$route.query.id}`
        `/api/app/teacher/info/${this.$route.query.id}`
      );
      console.log(res)
      this.jsList = res.data;
      this.jsList.attr = this.jsList.attr.filter(v => {
        return v.id == undefined;
      });
    },

    async ajaxTeacherMainCourse() {
      let {
        data: res
      } = await this.$http.post(
        // `https://www.365msmk.com/api/app/teacher/mainCourse`,
        `/api/app/teacher/mainCourse`,
        { limit: 10, page: 1, teacher_id: this.$route.query.id }
      );
      this.zhj = res.data;
    },

    async ajaxTeacherComment() {
      let { data: res } = await this.$http.post(`/api/app/teacher/comment`, {
        limit: 10,
        page: 1,
        teacher_id: Number(this.$route.query.id)
      });
      this.pj = res.data;
    },

    heart() {
        if(this.flag == 2){
             this.$http
        .get(
          `/api/app/teacher/collect/${this.$route.query.id}?page=1&&limit=2`
        )
        .then(res => {
          this.flag = res.data.data.flag
        });
        }else {
            this.$http
        .get(
          `/api/app/teacher/collect/${this.$route.query.id}?page=1&&limit=2`
        )
        .then(res => {
          this.flag = res.data.data.flag
        });
        }
     console.log(this.flag)
    }
  }
};
</script>

<style scoped lang="scss">
