  background: #f0f2f5;
  height: 100%;
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.header {
  height: 2.5rem;
  background: linear-gradient(180deg, #63b7fe, #4e7de3);
  header {
    height: 0.9rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.2rem;
    font-size: 0.36rem;
    color: #fff;
    box-sizing: border-box;
  }
}
.box {
    width: 100%;
  min-height: 109%;
  overflow: auto;
}
.teacher-info {
  margin: -1.3rem auto 0;
  padding: 0 0.3rem;
  background: #fff;
  border-radius: 0.15rem;
  box-sizing: border-box;
  font-size: 0.2rem;
  width: 94%;
  overflow: hidden;
  .ti-base {
    height: 1.5rem;
    display: flex;
    align-items: center;
    img {
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
    }
    div {
      flex: 1;
      margin-left: 0.15rem;
      p {
        text-align: left;
        margin: 0;
      }
      p:nth-of-type(1) {
        span:nth-of-type(1) {
          color: #595959;
          font-size: 0.3rem;
          line-height: 0.56rem;
        }
        font {
          color: #ea7a2f;
          margin-left: 0.2rem;
          font-size: 0.26rem;
        }
      }
      p:nth-of-type(2) {
        color: #b7b7b7;
        font-size: 0.26rem;
        line-height: 0.4rem;
      }
    }
    button {
      padding: 0.05rem 0.35rem;
      background: #ebeefe;
      border-radius: 0.4rem;
      font-size: 0.3rem;
      font-weight: 400;
      color: #4169ff;
      color: #eb6100;
      border: 0;
    }
    .flagt {
      color: #b7b7b7;
      font-size: 0.32rem;
    }
  }
  .ti-label {
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    width: 100%;
    height: 0.9rem;
    > li {
      background: #ffe4d3;
      color: #ea7a2f;
      padding: 0.05rem 0.2rem;
      border-radius: 0.3rem;
      font-size: 0.26rem;
    }
  }
}
.van-tabs {
  margin-top: 0.2rem;
}
.tj {
  width: 100%;
  padding: 0 0.4rem;
  box-sizing: border-box;
  background: #fff;
  font-size: 0.28rem;
  > li {
    display: flex;
    line-height: 1.02rem;
    color: #8c8c8c;
    padding-top: 0.24rem;
    > span {
      margin-left: 0.1rem;
      margin-right: 0.25rem;
    }
    font {
      flex: 1;
      text-align: left;
    }
  }
  > li:nth-last-of-type(1) {
    font {
      line-height: 0.5rem;
