      padding-top: 0.3rem;
    }
  }
}

.index-item-page {
  padding: 0.4rem;
  background: #fff;
  p {
    margin: 0;
  }
  .ii-item {
    background: #fff;
    box-shadow: 0 0.04rem 0.094rem 0 hsla(0, 0%, 81.2%, 0.35);
    border-radius: 0.15rem;
    padding: 0 0.35rem;
    margin-bottom: 0.4rem;
    // position: relative;
    .ii-title {
      padding-top: 0.4rem;
