      text-align: left;
    }
    .ii-teacher {
      height: 1.2rem;
      display: flex;
      align-items: center;
      > img {
        width: 0.72rem;
        height: 0.72rem;
        border-radius: 50%;
      }
      > font {
        font-size: 0.28rem;
        color: rgba(0, 0, 0, 0.45);
        margin-left: 0.2rem;
        font-weight: 400;
      }
    }
    .ii-info {
      border-top: 0.01rem solid #f5f5f5;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 1rem;
      .person {
        font-size: 0.28rem;
        color: rgba(0, 0, 0, 0.45);
      }
      .free {
        color: #44a426;
        font-size: 0.3rem;
      }
      .price {
        color: #ee1f1f;
