        > img {
          width: 0.32rem;
        }
      }
    }
  }
}
.yue {
  width: 100%;
  height: 0.8rem;
  background: #eb6100;
  color: #fff;
  font-size: 0.35rem;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  border: none;
  outline: none;
}
.no {
  font-size: 0.3rem;
}
</style>
