<template>
  <div class="home">
        <router-view></router-view>
    <van-tabbar route class="footer">
      <van-tabbar-item replace to="/" icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item replace to="/course" icon="search">课程</van-tabbar-item>
      <van-tabbar-item replace to="/record" icon="search">约课记录</van-tabbar-item>
      <van-tabbar-item replace to="/p" icon="search">练习</van-tabbar-item>
      <van-tabbar-item replace @click="to" icon="friends-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {};
  },
  props: {},
  created() {},
  mounted() {},
  methods: {
      to() {
      let user = window.localStorage.getItem('userToken')
      if(user) {
        this.$router.push('/mine')
      } else {
        this.$router.push('/Login')
      }
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>

<style>
.home{
    padding-bottom: 0.75rem;
    height: 100%;
}
.footer{
    background: #fff;
    height: 70px;
}
.van-tabbar-item--active {
    color: rgb(235, 97, 0);
}
.van-tabbar-item__icon{
    font-size: 26px;
    margin-top: 0.1rem;
}
.van-tabbar-item{
    font-size: 18px;
}
</style>