<template>
  <div>
    <header>
      <div></div>
      <div>设置密码</div>
      <div @click="jump">跳过</div>
    </header>
    <div class="input">
      <p>
        <input type="password" placeholder="请设置登录密码" v-model="passO" />
      </p>
      <p>
        <input type="password" placeholder="请再次输入密码" v-model="passT" />
      </p>
    </div>
    <div class="login">
      <div @click="Ok">确定</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      passO: "",
      passT: ""
    };
  },
  props: {},
  created() {},
  mounted() {},
  methods: {
    Ok() {
      if (this.passO === this.passT || this.passO != "" || this.passT != "") {
        let tel = window.localStorage.getItem("tel");
        let sms_code = window.localStorage.getItem("sms_code");
        this.$http
          .post("/api/app/password", {
            mobile: tel,
            password: this.passO,
            sms_code: sms_code
          })  
          .then(res => {
            console.log(res);
            this.$router.push('/Login')
          });
      }
    },
    jump() {
        this.$router.push('/')
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>

<style scoped lang="scss">
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 1rem;
  font-size: 0.35rem;
  padding: 0 0.2rem;
  box-sizing: border-box;
  font-weight: 500;
}
.input {
  box-sizing: border-box;
  width: 100%;
  padding: 0 0.45rem;
  > p {
    border-bottom: 0.01rem solid #ddd;
    margin: 0;
    > input {
      width: 100%;
      height: 0.9rem;
      line-height: 0.9rem;
      border: none;
      font-size: 0.28rem;
    }
  }
}
.login {
  width: 100%;
  > div {
    width: 90%;
    margin: 1.3rem auto;
    border-radius: 0.4rem;
    background: rgb(253, 78, 9);
    color: #fff;
    text-align: center;
    height: 0.9rem;
    line-height: 0.9rem;
    font-size: 0.3rem;
  }
}
</style>